import React from 'react';
import video from './videos/video.mp4'
import video2 from './videos/video2.mp4'
import video3 from './videos/video3.mp4'

class Header extends React.Component {
    constructor() {
        super()
        this.state = {
            fade: 'op100'
        }

        this.fadeIn = this
            .fadeIn
            .bind(this)
    }

    fadeIn() {
        const {fade} = this.state
        fade === 'op100' && this.setState({fade: 'op0'})
    }

    render() {

        const {fade} = this.state
        const className = `title-bloc ${fade} offset-15 col-9 offset-s-1 col-s-23 flex-col`
        return (
            <div className="col-25 head align-end">
                <div
                    className="col-14 col-s-25 video-container"
                    dangerouslySetInnerHTML={{
                    __html: `<video class="video" autoplay loop muted playsinline> <source src=${this.props.url} alt="video" type="video/mp4"/></video>`
                }}/>

                <div className={className} onLoad={this.fadeIn}>
                    <p className="text-s light mgbt-text-30">Lacoste gift guide</p>
                    <h1 className="big-title light">{this.props.title}</h1>
                    <p className="text-s light">Lorem ipsum Nam Donec malesuada leo, vehicula
                        commodo Morbi tincidunt lacus aliquet ultrices tempor scelerisque neque Cras sed
                        vitae eget Morbi rhoncus feugiat ultricies eget lectus erat Sed elit. Erat, a
                        Nam sagittis nisi vitae arcu. Turpis lorem, Donec ac eget metus risus. Justo,
                        sollicitudin lectus tempor lacinia.</p>
                </div>
            </div>
        )
    }
}

export default Header