import React from 'react'
import image1 from './images/image1.jpg'
import image2 from './images/image2.jpg'

class Tile extends React.Component {
    constructor() {
        super()
    }

    render() {
        const {img, alt, classy} = this.props
        return (
            <div className={classy}>
                <div className="row tile">
                    <img className="tileimg" src={img} alt={alt}/>
                    <div className="inner-bottom mgbt1">
                        <h3 className="inner-title light mgbt-text-10">For him</h3>
                        <a className="text-s light" href="#">all products</a>
                    </div>
                </div>
            </div>
        )
    }
}

class ProductTile extends React.Component {
    constructor() {
        super()
    }

    render() {
        const {img, alt, classy} = this.props

        return (
            <div className={classy}>
                <div className="row tile">
                    <img className="tileimg" src={img} alt={alt}/>
                </div>
                <a className="text-s dark mgt-text-10" href="#">See product</a>
            </div>
        )
    }
}

class Container extends React.Component {
    constructor() {
        super()
    }

    render() {
        const full = "offset-1 col-7 col-s-23 mgbt1 flex-col full"
        const mid = "offset-1 col-7 col-s-11 mgbt1 flex-col mid"
        return (
            <div className="row mgt2 mgbt2">
                <Tile classy={full} img={image1} alt="image"/>
                <ProductTile classy={mid} img={image2} alt="image"/>
                <ProductTile classy={mid} img={image2} alt="image"/>
                <ProductTile classy={full} img={image2} alt="image"/>
                <ProductTile classy={mid} img={image2} alt="image"/>
                <ProductTile classy={mid} img={image2} alt="image"/>

            </div>
        )
    }
}

export default Container